import React from 'react';
import 'antd/dist/antd.css';
import '../index.css';
import { Carousel, Button } from 'antd';
import MainContent from './MainContent';
import { DownOutlined, LeftOutlined, RightOutlined } from '@ant-design/icons';

import { Typography } from 'antd';


class MainPage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      collapsed: false,
      selectedIndex: 0,
    };

    this.carousel = React.createRef();
    this.myRef = React.createRef()

  }


  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  onClick = (item) => {
    console.log("clicked", item);
    this.setState({ selectedIndex: item.key });
  }

  onChange(a, b, c) {
    console.log(a, b, c);
  }

  goToRight = () => {
    this.carousel.next();
  }

  goToLeft = () => {
    this.carousel.prev();
  }

  executeScroll = () => this.myRef.scrollIntoView()

  render() {


    return (
      <div>
        <div style={{ position: "relative" }}>
          <Carousel ref={node => (this.carousel = node)} afterChange={this.onChange} autoplay >
            <div className='main-image-1'>
              <div className='main-div'>
                <h1 className='main-text-1'>;</h1>
                <h1 className='main-text'>i   .    .</h1>
                <DownOutlined />
                <Button shape="round" size="large">

                   
                </Button>
              </div>
            </div>
            <div className='main-image-2'>
              <div className='main-div'>
                <h1 className='main-text-1'>;</h1>
                <h1 className='main-text'>i   .    .</h1>
                <DownOutlined />
                <Button shape="round" size="large">

                   
                </Button>
              </div>
            </div>
            <div className='main-image-3'>
              <div className='main-div'>
                <h1 className='main-text-1'>;</h1>
                <h1 className='main-text'>i   .    .</h1>
                <DownOutlined />
                <Button shape="round" size="large">

                   
                </Button>
              </div>
            </div>
          </Carousel>
          <div style={{ position: "absolute", top: "50%", transform: "translate(50%, -50%)" }} onClick={this.goToLeft}>
            <LeftOutlined style={{ color: "white", fontSize: "45px" }} size={200} />
          </div>
          <div style={{ position: "absolute", top: "50%", right: "0%", transform: "translate(-50%, -50%)" }} onClick={this.goToRight}>
            <RightOutlined style={{ color: "white", fontSize: "45px" }} size={200} />
          </div>
          <div style={{ position: "absolute", top: "90%", right: "47%", transform: "translate(-50%, -50%)" }} onClick={() =>window.scrollTo(0,window.innerHeight)}>
            <DownOutlined style={{ color: "white", fontSize: "45px" }} size={200} />
          </div>
        </div>
        <MainContent ref={this.myRef}/>
      </div>
    );


  }
}

export default () => <MainPage />;